.App {
  text-align: center;
}
.App-header {
  background-color: coral;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.haeading{
  margin-top: 20px;
  color: white;
  background-color: #340744;
}
.main_div{
  height: 600px;
  width: 500px;
  background-color: white;
  text-align: center;
  border-radius: 10px;
}
.add_itemsBtn{
  height: 45px;
  width: 45px;
  border-radius: 100%;
  border: white;
  background-color: #A16AE8;
  color: white;
  font-size: 1.7rem;
  font-weight: 500;
  margin: 0px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.add_itemsBtn:hover{
  background-color: #340744;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}
.input_add{
  width: 280px;
  height: 40px;
  margin-right: 20px;
  border: none;
  border-bottom: 3px solid #A16AE8;
  text-align: center;
}
.input_add:focus{
  outline: none;
}
ol{
  margin-top: 30px;
}
ol li{
  color: black;
  list-style: none;
  margin-top: 20px;
}
.del_btn{
  height: 30px;
  width: 30px;
  margin-left: 30px;
  background-color: #FF5B5B;
  color: white;
  border: none;
  border-radius: 100%;
  font-size: 1.3rem;
  font-weight: 500;
}
.del_btn:hover{
  background-color: red;
}